.gpt3__whatgpt3 {
  display: flex;
  flex-direction: column;
  padding: 3rem;
  background: #f9f9f9;
  border-radius: 20px;
}
.immi__possibility-content-heading {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-bottom: 3rem;
}

.immi__possibility-content-heading h1 {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 34px;
  line-height: 45px;
  margin: 1rem 0;
}
.gpt3__whatgpt3-feature {
  display: flex;
  flex-direction: column;
  margin-left: 1rem;
}
.gpt3__whatgpt3-feature .gpt3__features-container__feature {
  margin: 0;
  margin-bottom: 2rem;
}
.gpt3__whatgpt3-feature .gpt3__features-container__feature-text {
  max-width: 700px;
}

@media screen and (max-width: 1100px) {
  /* Customizing feature component as per needs */
  .gpt3__whatgpt3-feature .gpt3__features-container__feature {
    flex-direction: column;
  }

  /* Customizing feature component as per needs */
  .gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
    margin-top: 0.5rem;
  }
}
